.app-header {
    height: 300px;
    max-width: 600px;
    margin: 0 auto;
    background-image: -webkit-linear-gradient(top, transparent, #DAC5B6),url(/static/media/pink_header.9ba73ec5.jpg);
    background-image: linear-gradient(to bottom, transparent, #DAC5B6),url(/static/media/pink_header.9ba73ec5.jpg);

    background-size:     cover;
    background-repeat:   no-repeat;
    background-position: center center;
}

.header-container {
    max-width: 600px;
    margin: 0 auto;
}

body {
    background-color: whitesmoke;
}
.tc-container {
    max-width: 600px;
    margin: 0 auto;
    height: 150px;
    background-color: #DAC5B6;
}
.p-container {
    margin: 0 auto;
    max-width: 600px;
    min-height: 400px;
    background: -webkit-linear-gradient(#DAC5B6, #D3AC8C);
    background: linear-gradient(#DAC5B6, #D3AC8C);
    padding: 30px 10px;
}

